<template>
  <div class="login">
    <h1 style="color:white;">Veuillez vous connecter</h1><br>
    <div id="form">
      <div v-if="firstStep">
        <vs-input icon-no-border icon="account_circle" label-placeholder="Pseudo" v-model="username" style="display:inline-block;vertical-align:baseline;margin-right:10px;text-align:left !important;"/>
        <vs-button type="relief" @click="firstSubmit" style="display:inline-block;vertical-align:baseline;">Suivant</vs-button>
      </div>
      <div v-if="secondStep">
        <p style="color:white;">Veuillez mettre le code suivant en humeur : <span style="padding: 6px;background-color:#656565;border: black solid 1px;border-radius:15px;">{{mottoCode}}</span> (le code a été copié)</p><br>
        <vs-button color="success" type="relief" @click="secondSubmit" style="display:inline-block;vertical-align:baseline;">Terminer</vs-button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: 'Login',
  components: {
  },
  data: function() {
    return {
      username: "",
      mottoCode: "",
      currentMotto: "",
      firstStep: true,
      secondStep: false,
      tempData: {}
    }
  },
  beforeCreate: function() {
    this.$parent.checkConnected()
  },
  methods:{
    openLoading(){
      this.$vs.loading({type:"radius"})
    },
    generateCode() {
       var length = 8
       var result           = '';
       var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
       var charactersLength = characters.length;
       for ( var i = 0; i < length; i++ ) {
          result += characters.charAt(Math.floor(Math.random() * charactersLength));
       }
       result = "#RFH_" + result
       return result;
    },
    firstSubmit(){
      var self = this
      this.openLoading()
      axios.get('https://www.habbo.fr/api/public/users?name=' + self.username)
      .then(response => {
        this.$vs.loading.close()
        // JSON responses are automatically parsed.
        console.log(response)
        this.$vs.notify({title:'Succès !',text:'Données récupérées ! Veuillez confirmer la connexion ',color:'success',position:'top-center'})
        this.mottoCode = this.generateCode()
        this.$copyText(this.mottoCode)
        this.firstStep = false
        this.secondStep = true
      })
      .catch(e => {
        this.$vs.loading.close()
        this.$vs.notify({title:'Erreur !',text:'Une erreur s\'est produite : ' + e ,color:'danger',position:'top-center'})
      })
    },
    secondSubmit(){
      var self = this
      this.openLoading()
      axios.get('https://www.habbo.fr/api/public/users?name=' + self.username) //Update the in-game motto
      .then(response => {
        this.$vs.loading.close()
        // JSON responses are automatically parsed.
        console.log(response.data.motto + " ?= " + this.mottoCode)
        self.currentMotto = response.data.motto //Save the motto to a more global var
        self.tempData = response.data
      })
      .catch(e => {
        this.$vs.loading.close()
        this.$vs.notify({title:'Erreur !',text:'Une erreur s\'est produite : ' + e ,color:'danger',position:'top-center'})
        return //Stopping the process
      })
      var attempt = 0 //Since the API may be slow, we give him 2 attempts
      if (this.currentMotto.indexOf(this.mottoCode) > -1) {
        this.$vs.notify({title:'Succès !',text:'Connexion en cours...',color:'success',position:'top-center'})
        this.$parent.login(this.tempData)
      }else{
        console.log("Failed")
        attempt++
        if(attempt < 2) {
          setTimeout(this.secondSubmit, 500);
        }else{
          this.$vs.notify({title:'Erreur !',text:'Code erroné, veuillez réessayer' ,color:'danger',position:'top-center'})
        }
      }
    },
  }
}
</script>

<style lang="stylus">
.login
  padding-top 10%
</style>
